.main_help_block {
	float: left;
	position: relative;
	margin-top: -402px;
	margin-left: 751px;
	min-height: 443px;
	width: 503px;
	background-color: #FFF;
	box-shadow: 4px 4px 30px 0 rgba(128, 128, 128, 0.2);

	.main_help_block_title {
		display: inline-block;
		margin-top: 40px;
		margin-left: 50px;
		margin-bottom: 39px;
		height: 26px;
		width: 109px;
		color: #333;
		font-family: 'Raleway-Bold', sans-serif;
		font-size: 20px;
		font-weight: bolder;
		line-height: 26px;
	}

	input[type=text],
	input[type=email],
	input[type=tel] {
		display: inline-block;
		color: #BABABA;
		font-family: 'Raleway-Regular', sans-serif;
		margin-left: 49px;
		font-size: 14px;
		line-height: 16px;
		padding-bottom: 10px;
		width: 181px;
		border: none;
		border-bottom: 1px solid #D5D5D5;
		text-overflow: ellipsis;
		outline: none;
	}

	input:-webkit-autofill {
		-webkit-box-shadow: 0 0 0 30px white inset !important;
		-webkit-text-fill-color: #BABABA !important;
	}

	input[type=text]:nth-of-type(1) {
		width: 181px;
		padding-bottom: 10px;
	}

	input[type=text]:nth-of-type(2) {
		width: 403px;
		margin-top: 25px;
		margin-bottom: 2px;
	}

	input[type=email] {
		margin-top: 17px;
		padding-bottom: 5px;
	}

	input[type=tel] {
		margin-top: 17px;
		margin-left: 39px;
		padding-bottom: 5px;
	}
}

.main_help_block input::placeholder {
	color: #A5A5A5;
	word-spacing: 1px;
}

.main_help_block input:-ms-input-placeholder { /* Internet Explorer 10-11 */
	color: #A5A5A5;
}

.main_help_block input::-ms-input-placeholder { /* Microsoft Edge */
	color: #A5A5A5;
}

.form_money_amount {
	display: flex;
	align-content: stretch;
	background: #F3F3F3;
	float: right;
	min-width: 181px;
	margin-top: -15px;
	margin-right: 45px;

	span {
		font-family: "ProximaNova-Regular", sans-serif;
		font-size: 12px;
		display: inline-block;
		cursor: pointer;
		padding: 15px 19px;
		color: #BABABA;

		&:hover,
		&:focus {
			background: linear-gradient(224.95deg, #F3AE43 0%, #E78B48 100%);
			color: #fff;
			font-weight: bold;
		}
	}
}

.money_donations_bg {
	background: linear-gradient(224.95deg, #F3AE43 0%, #E78B48 100%);
	color: #fff !important;
	font-weight: bold;
}

.help_form_radio_buttons_container {
	display: flex;
	justify-content: space-between;
	flex-wrap: nowrap;
	margin-top: 30px;
	margin-left: 50px;
	height: 45px;
	width: 402px;
	position: relative;
}

.single_donation {
	position: relative;
	visibility: hidden;
	width: 201px;
	height: 45px;
}

.monthly_donation {
	position: relative;
	visibility: hidden;
	width: 201px;
	height: 45px;
}

.single_donation + label {
	cursor: pointer;
	display: inline-block;
	position: absolute;
	text-align: center;
	left: 0px;
	top: 0px;
	background-color: #F3F3F3;
	width: 201px;
	height: 45px;

	i {
		margin-top: 15px;
		display: inline-block;
		height: 15px;
		width: 71px;
		color: #A5A5A5;
		font-family: $desk-light-proxima;
		font-size: 12px;
		font-weight: bold;
		line-height: 15px;
		font-style: normal !important;
	}

	&:hover,
	&:focus {
		background: linear-gradient(224.95deg, #F3AE43 0%, #E78B48 100%);

		i {
			color: #fff;
		}
	}
}

.monthly_donation + label {
	cursor: pointer;
	display: inline-block;
	position: absolute;
	text-align: center;
	right: 0px;
	top: 0px;
	background-color: #F3F3F3;
	width: 201px;
	height: 45px;

	i {
		margin-top: 15px;
		display: inline-block;
		height: 15px;
		width: 71px;
		color: #A5A5A5;
		font-family: $desk-light-proxima;
		font-size: 12px;
		font-weight: bold;
		line-height: 15px;
		font-style: normal !important;
	}

	&:hover,
	&:focus {
		background: linear-gradient(224.95deg, #F3AE43 0%, #E78B48 100%);

		i {
			color: #fff;
		}
	}
}

.checked_label {
	background: linear-gradient(224.95deg, #F3AE43 0%, #E78B48 100%);

	i {
		color: #fff !important;
	}
}

.main_help_block_tip {
	display: inline-block;
	color: #A1A1A1;
	margin-left: 52px;
	margin-top: 26px;
	color: #A5A5A5;
	font-family: 'Raleway-Regular', sans-serif;
	font-size: 12px;
	line-height: 14px;
}

.main_help_block_help {
	display: inline-block;
	position: relative;
	margin-left: 45px;
	margin-top: 39px;
	margin-bottom: 40px;
	margin-right: 123px;
	color: #E78B48;
	border: none !important;
	outline: none !important;
	background: none !important;
	font-family: "Raleway-Bold", sans-serif;
	font-size: 14px;
	font-weight: bold;
	line-height: 16px;
	overflow: visible;
	cursor: pointer;

	&:hover,
	&:focus {
		margin-left: 47px;
		transition: .4s;
		opacity: 0.7;
	}
}

.main_help_block_help::after {
	content: '';
	position: absolute;
	top: 2px;
	left: calc(100% + 8px);
	width: 12px;
	height: 13px;
	background: url("../img/svg/arrow_right.svg") no-repeat center;
}

.main_help_block label.offer_label {
	display: inline-block;
	margin-top: 39px;
	color: #A5A5A5;
	font-family: 'Raleway-Regular', sans-serif;
	font-size: 14px;
	line-height: 16px;

	b {
		color: inherit;
		font-weight: normal;

		a {
			text-decoration: underline;
			color: #A5A5A5;
		}

		a::after {
			content: '*';
		}
	}
}

.main_help_block input[type='checkbox'] {
	width: 13px;
	height: 13px;
	margin-top: 40.7px;
	margin-right: 10px;
	display: inline-block;
	position: relative;
	cursor: pointer;
	outline: 1px solid #BFBFBF;
	border: unset;
}

.main_help_block input[type='checkbox']:after {
	content: "";
	vertical-align: middle;
	text-align: center;
	line-height: 13px;
	position: absolute;
	cursor: pointer;
	height: 13px;
	width: 13px;
	left: 0;
	top: 0;
	font-size: 10px;
	background: #fff;
}

.main_help_block input[type='checkbox']:checked:after {
	background: #fff;
	content: '\2714';
	color: #E78B48;
}

