//MEDIA QUERY 320

@media screen and (max-width: 320px) {
	.about_us {
		display: inline-block;
		margin-top: 0px;
		width: 100%;
		font-family: $desk-regular-raleway;
		margin-bottom: 0px;

		.about_us_title {
			height: 48px;
			width: 250px;
			margin-left: 35px;
			margin-top: 103px;
			font-family: $desk-bold-raleway;
			font-weight: normal;
			font-size: 24px;
			line-height: 34px;
		}

		.about_us_title::before {
			top: 118px;
			width: 20px;
		}
	}

	.about_us .about_us_item_first {
		margin-top: 10px;
		min-height: 740px;
		width: 295px;
		margin-left: 15px;
		border-bottom: none;

		img:nth-of-type(1) {
			display: block;
			float: left;
			width: 295px;
			min-height: 289px;
			height: 289px;
		}

		div {
			display: inline-block;
			position: relative;
			float: none;
			min-width: 100%;
			background: #fff;
			min-height: 449px;

			span {
				display: inline-block;
				margin-top: 41px;
				margin-left: 20px;
				width: 260px;
				min-height: 312px;
				color: #333;
				font-family: $desk-regular-raleway;
				word-spacing: 1px;
				font-size: 14px;
				line-height: 24px;
			}

			i {
				position: relative;
				display: inline-block;
				font-style: normal;
				margin-top: 40px;
				margin-bottom: 40px;
				margin-left: 20px;
				color: $arrowcolor;
				font-family: $desk-bold-raleway;
				font-size: 14px;
				line-height: 17px;

				&:hover,
				&:focus {
					margin-left: 26px;
					transition: .4s;
					opacity: 0.7;
				}

				a {
					text-decoration: none;
					color: inherit;
				}
			}

			i::after {
				content: '';
				position: absolute;
				top: 1px;
				left: calc(100% + 10px);
				width: 12px;
				height: 13px;
				background: url("../img/svg/arrow_right.svg") no-repeat center;
			}
		}
	}

	.about_us_item_second {
		margin-top: 0px;
		margin-left: 15px;
		flex-direction: column;
		width: 295px;
		min-height: 550px;

		span {
			min-height: 181px;
			min-width: 100%;
			color: #333;
			font-family: $desk-bold-raleway;
			font-size: 14px;
			line-height: 22px;
			border: none;
			border-top: 1px solid rgba(165, 165, 165, 0.25);

			b {
				margin-top: 11px;
				margin-left: 20px;
				width: 250px;
				white-space: normal;
				color: inherit;
				font-family: inherit;
				font-size: inherit;
				line-height: inherit;
				font-weight: normal;
			}
		}
	}

	.about_us_item_second span.Item1 {
		padding-right: 0px;
	}

	.about_us_item_second .Item1 i,
	.about_us_item_second .Item2 i,
	.about_us_item_second .Item3 i {
		display: inline-block;
		font-style: normal;
		position: relative;
		margin-bottom: 20px;
		margin-left: 20px;
		color: $arrowcolor;
		font-family: $desk-bold-raleway;
		font-size: 14px;
		line-height: 17px;

		&:hover,
		&:focus {
			margin-left: 26px;
			transition: .4s;
			opacity: 0.7;
		}

		a {
			text-decoration: none;
			color: inherit;
		}
	}

	.about_us_item_second .Item1 i::after,
	.about_us_item_second .Item2 i::after,
	.about_us_item_second .Item3 i::after {
		content: '';
		position: absolute;
		top: 1px;
		left: calc(100% + 10px);
		width: 12px;
		height: 13px;
		background: url("../img/svg/arrow_right.svg") no-repeat center;
	}

	.about_us_item_second span:hover,
	.about_us_item_first div:hover,
	.about_us_item_first div:focus,
	.about_us_item_second span:focus {
		background: $Bghover-color;

		span {
			color: #fff;
		}

		b {
			color: #fff;
		}

		i {
			color: #fff;
		}

		i::after {
			content: '';
			background: url("../img/svg/arrow_right_white.svg") no-repeat center;
		}
	}
}

