// NEWS BLOCK GROUP MOBILE STYLES

.news_block_group_mobile {
	margin: auto;
	display: flex;
	align-content: stretch;
	justify-content: space-between;
	flex-wrap: wrap;
	width: 1140px;
	margin-bottom: 0;

	div {
		position: relative;
		color: #fff;
		min-width: 353px;
		min-height: 340px;
		background: #fff;
		box-shadow: 4px 4px 20px 0 rgba(209, 209, 209, 0.2);
		cursor: pointer;
		margin-bottom: 40px;
	}
}

.news_block_group_item1,
.news_block_group_item2,
.news_block_group_item3,
.news_block_group_item4,
.news_block_group_item5,
.news_block_group_item6 {
	span:nth-of-type(1) {
		display: inline-block;
		margin-left: 40px;
		margin-right: 101px;
		margin-top: 30px;
		height: 16px;
		width: 71px;
		color: #A5A5A5;
		font-family: 'Raleway-Regular', sans-serif;
		font-size: 14px;
		line-height: 16px;
		letter-spacing: 1px;
		text-transform: uppercase;
	}

	span:nth-of-type(1)::after {
		content: '';
		position: absolute;
		top: 66px;
		left: 40px;
		height: 1px;
		width: 273px;
		opacity: 0.25;
		background-color: #A5A5A5;
	}

	span:nth-of-type(2) {
		display: inline-block;
		margin-top: 30px;
		height: 17px;
		width: 100px;
		color: #A5A5A5;
		font-family: "ProximaNova-Light", sans-serif;
		font-size: 14px;
		line-height: 17px;
	}
}

.news_block_group_item1 h2,
.news_block_group_item2 h2,
.news_block_group_item4 h2,
.news_block_group_item5 h2 {
	margin-left: 42px;
	margin-top: 51px;
	margin-bottom: 0;
	color: #333;
	font-family: 'Raleway-Bold', sans-serif;
	font-size: 20px;
	white-space: nowrap;
	line-height: 24px;
}

.news_block_group_item2 b,
.news_block_group_item5 b {
	display: inline-block;
	margin-left: 42px;
	margin-top: -5px;
	white-space: pre-line;
	color: #333;
	font-weight: normal;
	font-family: 'Raleway-Regular', sans-serif;
	font-size: 16px;
	line-height: 26px;
}

.news_block_group_item1 b,
.news_block_group_item4 b {
	display: inline-block;
	margin-left: 42px;
	margin-top: -5px;
	white-space: pre-line;
	color: #333;
	font-weight: normal;
	font-family: 'Raleway-Regular', sans-serif;
	font-size: 16px;
	line-height: 26px;
}

.news_block_group_item3 h2,
.news_block_group_item6 h2 {
	white-space: pre-line;
	margin-left: 42px;
	margin-top: 26px;
	margin-bottom: 0px;
	color: #333;
	font-family: 'ProximaNova-Bold', sans-serif;
	font-size: 20px;
	line-height: 24px;
}

.news_block_group_item3 b,
.news_block_group_item6 b {
	display: inline-block;
	margin-top: 0px;
	margin-left: 42px;
	white-space: pre-line;
	color: #333;
	font-weight: normal;
	font-family: 'Raleway-Regular', sans-serif;
	font-size: 16px;
	line-height: 26px;
}

.news_block_group_item1 a,
.news_block_group_item4 a {
	font-style: normal;
	display: block;
	color: #E78B48;
	font-family: 'Raleway-Bold', sans-serif;
	font-size: 14px;
	font-weight: bold;
	line-height: 16px;
	white-space: nowrap;
	position: relative;
	margin-top: 35px;
	margin-left: 40px;
	margin-bottom: 40px;
	text-decoration: none;

	&:hover,
	&:focus {
		margin-left: 46px;
		transition: .4s;
		opacity: 0.7;
	}
}

.news_block_group_item2 a,
.news_block_group_item5 a {
	font-style: normal;
	display: block;
	color: #E78B48;
	font-family: 'Raleway-Bold', sans-serif;
	font-size: 14px;
	font-weight: bold;
	line-height: 16px;
	white-space: nowrap;
	position: relative;
	margin-top: 38px;
	margin-left: 40px;
	margin-bottom: 40px;
	text-decoration: none;

	&:hover,
	&:focus {
		margin-left: 46px;
		transition: .4s;
		opacity: 0.7;
	}
}

.news_block_group_item3 a,
.news_block_group_item6 a {
	font-style: normal;
	display: block;
	color: #E78B48;
	font-family: 'Raleway-Bold', sans-serif;
	font-size: 14px;
	font-weight: bold;
	line-height: 16px;
	white-space: nowrap;
	position: relative;
	margin-top: 38px;
	margin-left: 38px;
	margin-bottom: 40px;
	text-decoration: none;

	&:hover,
	&:focus {
		margin-left: 46px;
		transition: .4s;
		opacity: 0.7;
	}
}

.news_block_group_mobile div:hover,
.news_block_group_mobile div:focus {
	h2,
	span,
	b,
	a {
		color: #fff;
	}

	a::after {
		content: '';
		background: url("../img/svg/arrow_right_white.svg") no-repeat center !important;
	}

	span:nth-of-type(1)::after {
		content: '';
		background: #fff;
	}

	background: linear-gradient(224.05deg, #F3AE43 0%, #E78B48 100%);
}

.news_block_group_mobile div a::after {
	content: '';
	position: absolute;
	top: 1px;
	left: calc(38% + 10px);
	width: 12px;
	height: 13px;
	background: url("../img/svg/arrow_right.svg") no-repeat center;
}

.news_more_mobile {
	position: relative;
	margin-top: 7px;
	margin-left: 573px;
	width: 300px;
	z-index: 5;

	&:hover,
	&:focus {
		opacity: 0.7;
		transition: .4s;
		margin-left: 578px;
	}

	ul {
		display: inline-block;
		width: 50px;
		list-style-type: none;

		li {
			display: inline-block;
			height: 3px;
			width: 3px;
			margin-right: 2px;
			background-color: #E78B48;
		}
	}

	button.to_more_news_mobile {
		display: inline-block;
		margin-left: -33px;
		background: none;
		outline: none;
		border: none;
		color: #E78B48;
		font-family: 'Raleway-Bold', sans-serif;
		font-size: 14px;
		font-weight: bold;
		word-spacing: 2px;
		line-height: 16px;
	}
}

.news_block_group_item1,
.news_block_group_item5,
.news_block_group_item6,
.additional_news1,
.additional_news2,
.additional_news3,
.additional_news4,
.additional_news5,
.additional_news6 {
	display: none;
}

