		height: 13px;
		background: url("../img/svg/arrow_right.svg") no-repeat center;
	}

	.news_more {
		margin-top: 7px;
		margin-left: 410px;

		&:hover,
		&:focus {
			opacity: 0.7;
			margin-left: 416px;
		}
	}
}

