.dacha_center {
	position: relative;
	display: inline-block;
	min-height: 664px;
	width: 100%;
	margin-bottom: 53px;

	.dacha_center_title {
		display: inline-block;
		margin-left: 113px;
		margin-top: 50px;
		height: 58px;
		max-width: 860px;
		white-space: nowrap;
		color: #333;
		font-family: "Raleway-Bold", sans-serif;
		font-size: 48px;
		font-weight: bold;
		line-height: 58px;
		margin-bottom: 40px;
	}

	.dacha_center_title::before {
		content: '';
		position: absolute;
		left: 0;
		top: 77px;
		height: 3px;
		width: 58px;
		background-color: #E78B48;
	}
}

.dacha_center_tip {
	display: inline-block;
	width: 819px;
	color: #333333;
	font-family: 'Raleway-Regular', sans-serif;
	font-size: 16px;
	font-weight: 400;
	line-height: 28px;
	margin-left: 115px;
	margin-bottom: 60px;
}

.dacha_center_project {
	margin: auto;
  display: flex;
  align-content: stretch;
	position: relative;
	min-height: 394px;
	width: 1140px;
	background-color: #FFF;
	box-shadow: 4px 4px 30px 0 rgba(209, 209, 209, 0.2);

  .dacha_center_project_image_container {
  min-width: 573px;
  min-height: 100%;
  position: relative;
  
	img:nth-of-type(1) {
		display: inline-block;
    min-width: 100%;
    min-height: 100%;
	 }
 }
}

.dacha_center_project_details {
min-height: 100%;
min-width: 567px; 

span:nth-of-type(1) {
		display: inline-block;
    position: relative;
		text-transform: uppercase;
		margin-top: 30px;
		margin-left: 49px;
		color: #A5A5A5;
		font-family: 'Raleway-Regular', sans-serif;
		font-size: 12px;
		line-height: 14px;
	}

	span:nth-of-type(1)::after {
		content: '';
		position: absolute;
		top: 35px;
		left: 0px;
		height: 1px;
		width: 470px;
		opacity: 0.25;
		background-color: #A5A5A5;
	}
}

.dacha_center_project h2 {
	display: block;
	margin-top: 51px;
  margin-bottom: 0px;
	margin-left: 49px;
	color: #333;
	font-family: 'Raleway-Bold', sans-serif;
	font-size: 20px;
	white-space: nowrap;
	line-height: 24px;
}

.dacha_center_project span:nth-of-type(2) {
	display: inline-block;
  margin-top:-5px;
	margin-left: 49px;
  white-space:pre-line;
	color: #333;
	font-family: 'Raleway-regular', sans-serif;
	font-size: 16px !important;
	line-height: 26px !important;
}

.dacha_center_project a:nth-of-type(1) {
	display:inline-block;
  position:relative;
	margin-top: 38px;
	margin-left: 49px;
  margin-bottom: 40px;
	text-decoration: none;
	color: #E78B48;
	font-family: 'Raleway-Bold', sans-serif;
	font-size: 14px;
	font-weight: bold;
	line-height: 16px;
  overflow: visible;

  &:hover,
  &:focus {
  margin-left: 55px;
  transition: .4s;
  opacity:0.7;  
  }
}

.dacha_center_project a:nth-of-type(1)::after {
	content: '';
	position: absolute;
	top: 2px;
	left: calc(100% + 10px);
	width: 12px;
	height: 13px;
	background: url("../img/svg/arrow_right.svg") no-repeat center;
}

.dacha_center_project_received {
	display:inline-block;
	margin-top: 65px;
	margin-left: 49px;
  margin-right:8px;
	color: #333333;
	font-family: "ProximaNova-Bold", sans-serif;
  font-weight:bold;
	font-size: 16px;
	white-space: nowrap;
	line-height: 20px;
}

.dacha_center_project b {
	display:inline-block;
	margin-top: 65px;
  margin-right: 163px;
	font-weight: normal;
	color: #333333;
	font-family: 'Raleway-Regular', sans-serif;
	font-size: 16px;
	line-height: 19px;
}

.dacha_center_project_total {
	display:inline-block;
	margin-top: 65px;
	color: #E78B48;
	font-family: "ProximaNova-Bold", sans-serif;
	font-size: 16px;
  font-weight:bold;
	white-space: nowrap;
	line-height: 20px;
}

.dacha_center_project_total::after,
.dacha_center_project_received::after {
	content: '.';
}

.dacha_center_progress {
	display: inline-block;
  position: relative;
	margin-left: 49px;
	margin-top: 18px;
	width: 470px;
	height: 1px;
	background-color: #A5A5A5;
	border: none; /* Needed for Firefox */
	color: #A5A5A5; /* For IE10 */
}

progress::after {
	content: '';
	position: absolute;
	top: -1px;
	right: 0;
	height: 3px;
	width: 3px;
	background-color: #E78B48;
}

progress::-webkit-progress-bar {
	position: relative;
	height: 1px;
	background-color: #A5A5A5;
}

progress::-webkit-progress-value {
	position: absolute;
	top: -1px;
	height: 3px;
	background-color: #E78B48;
	transition: width 3s;
}

progress::-moz-progress-bar {
	background: #E78B48 !important;
	border: none !important;
	height: 3px !important;
	margin-top: -1px;
}

progress::-ms-fill {
	background: #E78B48 !important;
	color: #A5A5A5 !important;
	height: 3px !important;
}

