//MOBILE TABS STYLE

.our_activity_item_group_mobile {
	display: flex;
	justify-content: space-between;
	margin-top: 60px;
	margin-left: 113px;
	width: 1140px;
	min-height: 276px;
	position: relative;
}

.our_activity_item_group_mobile .our_activity_item_group_mobile_item1 {
	position: relative;
	font-family: 'Raleway-Bold', sans-serif;
	min-height: 276px;
	width: 550px;
	background-color: #FFF;
	box-shadow: 4px 4px 30px 0 rgba(209, 209, 209, 0.2);
}

.our_activity_item_group_mobile .our_activity_item_group_mobile_item2 {
	position: relative;
	font-family: 'Raleway-Bold', sans-serif;
	min-height: 276px;
	width: 550px;
	background-color: #FFF;
	box-shadow: 4px 4px 30px 0 rgba(209, 209, 209, 0.2);
}

.active_tab {
	color: #E78B48 !important;
	font-weight: bold !important;
	font-family: $desk-bold-raleway !important;
}

.active_tab::before {
	content: '';
	position: absolute;
	top: 8px;
	left: -20px;
	height: 3px;
	width: 10px;
	background-color: #E78B48;
}

.active_tab_second {
	color: #E78B48 !important;
	font-weight: bold !important;
	font-family: $desk-bold-raleway !important;
}

.active_tab_second::before {
	content: '';
	position: absolute;
	top: 8px;
	left: -58px;
	height: 3px;
	width: 40px;
	background-color: #E78B48;
}

//MEDIA QUERY 320px

@media screen and (max-width: 320px) {
	.our_activity {
		display: inline-block;
		width: 100%;
		margin-top: 30px;
		margin-bottom: 45px;

		.our_activity_title {
			height: 14px;
			width: 270px;
			color: #333333;
			font-family: $desk-bold-raleway;
			font-weight: normal;
			font-size: 24px;
			line-height: 34px;
			margin-left: 35px;
			margin-top: 0px;
		}

		.our_activity_title::before {
			top: 15px;
			height: 3px;
			width: 20px;
		}
	}

	.our_activity .our_activity_tip {
		color: #333333;
		width: 250px;
		font-family: $desk-regular-proxima;
		font-size: 14px;
		line-height: 24px;
		margin-left: 35px;
		margin-top: 20px;
		white-space: normal;
	}

	.our_activity_item_group_mobile {
		flex-direction: column;
		justify-content: unset;
		margin-left: 15px;
		margin-top: 40px;
		width: 290px;
		min-height: 1063px;
		font-family: $desk-regular-raleway !important;
	}

	.our_activity_item_group_mobile h2 {
		margin-top: 43px;
		margin-left: 20px;
		margin-bottom: 40px;
		white-space: nowrap;
		color: #333333;
		font-family: $desk-bold-raleway;
		font-size: 18px;
		font-weight: bold;
		line-height: 26px;
	}

	.our_activity_item_group_mobile .our_activity_item_group_mobile_item1 {
		min-height: 563px;
		width: 290px;
		margin-bottom: 20px;
	}

	.our_activity_item_group_mobile .our_activity_item_group_mobile_item2 {
		min-height: 460px;
		width: 290px;
	}

	.our_activity_item_group_mobile .our_activity_item_group_mobile_item1 div,
	.our_activity_item_group_mobile .our_activity_item_group_mobile_item2 div {
		width: 100%;
		min-height: 102px;
		border-top: 1px solid rgba(165, 165, 165, 0.25);

		b {
			display: inline-block;
			position: relative;
			white-space: nowrap;
			margin-left: 20px;
			margin-top: 40px;
			margin-bottom: 20px;
			color: #333333;
			font-family: $desk-regular-raleway;
			font-weight: normal;
			font-size: 16px;
			line-height: 22px;
		}

		span {
			display: inline-block;
			margin-left: 19px;
			color: #333333;
			font-family: $desk-regular-proxima;
			font-size: 14px;
			line-height: 24px;
			margin-bottom: 40px;
		}

		a {
			display: block;
			margin-bottom: 40px;
			margin-left: 19px;
			position: relative;
			text-decoration: none;
			color: $arrowcolor;
			color: #E78B48;
			font-family: $desk-bold-raleway;
			font-size: 14px;
			line-height: 16px;

			&:hover,
			&:focus {
				margin-left: 25px;
				transition: .4s;
				opacity: 0.7;
			}
		}

		a:after {
			content: '';
			position: absolute;
			top: 1px;
			left: calc(43% + 10px);
			width: 12px;
			height: 13px;
			background: url("../img/svg/arrow_right.svg") no-repeat center;
		}
	}

	.active_tab {
		color: #E78B48 !important;
		font-weight: bold !important;
		font-family: $desk-bold-raleway !important;
	}

	.active_tab_second {
		color: #E78B48 !important;
		font-weight: bold !important;
		font-family: $desk-bold-raleway !important;
	}

	.active_tab::before {
		content: '';
		position: absolute;
		top: 8px;
		left: -20px;
		height: 3px;
		width: 10px;
		background-color: #E78B48;
	}

	.active_tab_second::before {
		content: '';
		position: absolute;
		top: 8px;
		left: -20px;
		height: 3px;
		width: 10px;
		background-color: #E78B48;
	}

	.our_activity_item_group_mobile .our_activity_item_group_mobile_item1 div .during_treatment_tab_info_mobile,
	.our_activity_item_group_mobile .our_activity_item_group_mobile_item1 div .after_treatment_tab_info_mobile,
	.our_activity_item_group_mobile .our_activity_item_group_mobile_item2 div .migrants_pension_tab_info_mobile,
	.our_activity_item_group_mobile .our_activity_item_group_mobile_item1 div .during_treatment_tab_info_more_mobile,
	.our_activity_item_group_mobile .our_activity_item_group_mobile_item1 div .after_treatment_tab_info_more_mobile,
	.our_activity_item_group_mobile .our_activity_item_group_mobile_item2 div .migrants_pension_tab_info_more_mobile {
		display: none;
	}

	.displaySpanBlock {
		display: inline-block !important;
	}

	.displayArrowBlock {
		display: block !important;
	}
}

