// MEDIA QUERY 320

@media screen and (max-width: 320px) {
	.main {
		margin-top: -20px;
		width: 100%;
		height: 422px;
		min-height: 422px;
		font-family: $desk-regular-raleway;
		background: url("../img/content/Main_bg.png") no-repeat center;

		h1.main_title {
			color: #FFFFFF;
			font-family: $desk-bold-proxima;
			font-size: 24px;
			line-height: 34px;
			margin-top: 70px;
			margin-left: 15px;
			word-spacing: 2.5px;
			font-weight: normal;
		}

		span.main_title_tip {
			display: none;
		}

		.main_title_tip_more {
			margin-left: 15px;
			margin-top: 40px;
			font-family: $desk-bold-raleway !important;

			&:hover,
			&:focus {
				margin-left: 21px;
				opacity: 0.7;
				transition: .4s;
			}
		}
	}
}

