@media screen and (min-width: 1025px) and (max-width: 1100px) {
	.main {
		margin-top: 15px;

		h1.main_title {
			margin-top: 105px;
			margin-left: 20px;
			font-size: 24px;
			line-height: 34px;
			width: 450px;
			white-space: normal;
		}

		span.main_title_tip {
			margin-left: 20px;
			margin-top: 40px;
			white-space: normal;
			width: 400px;
		}

		.main_title_tip_more {
			margin-left: 20px;
		}
	}

	.main span.main_title_tip_more::after {
		content: '';
		left: 140px;
	}

	.main span.main_title_tip_more:hover,
	.main span.main_title_tip_more:focus {
		margin-left: 26px;
	}
}

