// OTHER_PROJECTS_LIST_MOBILE STYLES

.other_projects {
	width: 100%;
	position: relative;
}

.other_projects_list_mobile {
	display: flex;
	position: relative;
	width: 1140px;
  margin: auto;
  align-content: stretch;
	justify-content: space-between;
	flex-wrap: wrap;

	div {
		position: relative;
		min-height: 580px;
		min-width: 353px;
    margin-bottom: 40px;
		background-color: #FFF;
		box-shadow: 4px 4px 30px 0 rgba(209, 209, 209, 0.2);

    img {
     width: 353px;
     height: 258px;
    }

		b {
			position: absolute;
			top: 159px;
			right: 240px;
			color: #fff;
			font-family: 'Raleway-Regular', sans-serif;
			font-size: 16px;
			font-weight: normal;
			line-height: 19px;
      white-space: nowrap;
		}

		span:nth-of-type(1) {
			display: inline-block;
			position: absolute;
			top: 183px;
			right: 223px;
			color: #FFF;
			font-family: "ProximaNova-Bold", sans-serif;
			font-size: 16px;
			font-weight: bold;
			line-height: 20px;
		}

		span:nth-of-type(2) {
			position: absolute;
			top: 183px;
			right: 35px;
			color: #E78B48;
			font-family: "ProximaNova-Bold", sans-serif;
			font-size: 16px;
			font-weight: bold;
			line-height: 20px;
		}
	}
}

.other_projects_list_item1 span:nth-of-type(1) {
	right: 213px;
}

.other_projects_list_item1 span:nth-of-type(2) {
	right: 35px;
}

.other_projects_list_item1.lviv span:nth-of-type(1) {
	right: 223px;
}

.other_projects_list_item1.lviv span:nth-of-type(2) {
	right: 22px;
}

.other_projects_list_mobile div span:nth-of-type(1)::after,
.other_projects_list_mobile div span:nth-of-type(2)::after {
	content: '.';
}

.other_projects_list_progress {
	position: absolute;
	left: 43px;
	top: 224px;
	width: 273px;
	height: 1px;
	background-color: #A5A5A5;
	color: #A5A5A5;
	border: none;
}

.other_projects_list_progress::after {
	content: '';
	position: absolute;
	top: -1px;
	right: 0;
	height: 3px;
	width: 3px;
	background-color: #E78B48;
}

.other_projects_list_item1 span:nth-of-type(3),
.other_projects_list_item2 span:nth-of-type(3),
.other_projects_list_item3 span:nth-of-type(3) {
	position: absolute;
	text-transform: uppercase;
	top: 280px;
	left: 40px;
  white-space:nowrap;
	color: #A5A5A5;
	font-family: 'Raleway-Regular', sans-serif;
	font-size: 13px;
	line-height: 15px;
}

.other_projects_list_item1 span:nth-of-type(3)::after,
.other_projects_list_item2 span:nth-of-type(3)::after,
.other_projects_list_item3 span:nth-of-type(3)::after {
	content: '';
	position: absolute;
	bottom: -20px;
	left: 0px;
	width: 273px;
	height: 1px;
	background: #A5A5A5;
	opacity: 0.25;
}

.other_projects_list_item1 h2.item_title,
.other_projects_list_item2 h2.item_title,
.other_projects_list_item3 h2.item_title {
  margin-top: 50px;
	margin-left: 41px;
	margin-bottom: 5px;
	color: #333333;
	font-family: 'Raleway-Bold', sans-serif;
	font-size: 20px;
	line-height: 24px;
	word-spacing: unset;
  white-space: pre-line;
}

.other_projects_list_item1 p.item_title_tip,
.other_projects_list_item2 p.item_title_tip,
.other_projects_list_item3 p.item_title_tip {
  display: inline-block;
  white-space:pre-line;
	margin-left: 40px;
	color: #333333;
	font-family: 'Raleway-Regular', sans-serif;
	font-size: 16px;
	line-height: 26px;
}

.other_projects_list_item2 p.item_title_tip {
	display: inline-block;
	margin-top: 40px;
	margin-left: 41px;
}

.other_projects_list_item1 span:nth-of-type(4),
.other_projects_list_item2 span:nth-of-type(4),
.other_projects_list_item3 span:nth-of-type(4) {
	display: block;
  position: relative;
	margin-bottom: 40px;
	margin-left: 41px;
	text-decoration: none;
	color: #e78b48;
	font-family: 'Raleway-Bold',sans-serif;
	font-size: 14px;
	font-weight: 700;
	line-height: 16px;
  cursor: pointer;

	a {
		text-decoration: none;
		color: inherit;
	}
}

.other_projects_list_item1 span:nth-of-type(4)::after,
.other_projects_list_item2 span:nth-of-type(4)::after,
.other_projects_list_item3 span:nth-of-type(4)::after {
	content: '';
	position: absolute;
	top: 1px;
	left: calc(100% + 10px);
	width: 12px;
	height: 13px;
	background: url("../img/svg/arrow_right.svg") no-repeat center;
}

.other_mobile_projects_more {
  position:relative;
  z-index: 5;
	margin-top: 47px;
	margin-left: 573px;
	width: 300px;

	&:hover,
	&:focus {
		opacity: 0.7;
		transition: .4s;
		margin-left: 578px;
	}

	ul {
		display: inline-block;
		width: 50px;
		list-style-type: none;

		li {
			display: inline-block;
			height: 3px;
			width: 3px;
			margin-right: 2px;
			background-color: #E78B48;
		}
	}

	button.to_more_projects_mobile {
		display: inline-block;
		margin-left: -28px;
		height: 16px;
		min-width: 112px;
		background: none;
		outline: none;
		border: none;
		color: #E78B48;
		font-family: 'Raleway-Bold', sans-serif;
		font-size: 14px;
		font-weight: bold;
		word-spacing: 2px;
		line-height: 16px;
	}
}

.other_projects a { 
    text-decoration: none;
}

