	}

	.main_title_tip_more {
	  display: inline-block;
    position: relative;
		white-space: nowrap;
		margin-top: 57px;
		margin-left: 114px;
		color: #FFF;
		font-family: 'Raleway-Bold', sans-serif;
		font-weight: bold;
		font-size: 14px;
		line-height: 16px;
    overflow: visible;
    text-decoration: none;

		&:hover,
		&:focus {
			margin-left: 120px;
			opacity: 0.7;
			transition: .4s;
		}
