	margin-left: 113px;
	margin-top: -3px;
	cursor: pointer;
	box-shadow: 4px 4px 30px 0 rgba(209, 209, 209, 0.2);

	span {
		display: block;
    min-width: 379px; 
    min-height: 360px;
