		color: #333;
		white-space: pre-line;
		font-family: 'Raleway-Bold', sans-serif;
		font-size: 20px;
		font-weight: bold;
		line-height: 30px;
		border-right: 1px solid rgba(165, 165, 165, 0.25);

    b strong {
    font-family: $desk-bold-proxima;  
    }

		i {
      position: relative;
			font-style: normal;
			color: inherit;
			font-family: inherit;
			font-size: 14px;
			font-weight: bold;
			line-height: 16px;
			margin-left: 76px;
      margin-top: 40px;
      margin-bottom: 0px;
      white-space: nowrap;

