				text-decoration: none;
				color: inherit;
			}
		}

		i::after {
			content: '';
			position: absolute;
			top: 2px;
			left: calc(100% + 10px);
			width: 12px;
			height: 13px;
			background: url("../img/svg/arrow_right.svg") no-repeat center;
