//MEDIA QUERY 320

@media screen and (max-width: 320px) {
	.our_achievements {
		display: block;
		margin-top: 0px;
		width: 100%;

		br:nth-of-type(1) {
			display: block;
		}

		br:nth-of-type(2) {
			display: none;
		}
	}

	.our_achievements .our_achievements_title {
		height: 102px;
		width: 280px;
		color: #333333;
		font-family: $desk-bold-raleway !important;
		font-weight: normal;
		font-size: 24px;
		line-height: 34px;
		margin-left: 35px;
		margin-bottom: 50px;
		margin-top: 60px;

		br:nth-of-type(1) {
			display: block;
		}

		br:nth-of-type(2) {
			display: none;
		}
	}

	@-moz-document url-prefix() {
		.our_achievements .our_achievements_title {
			br:nth-of-type(1) {
				display: none;
			}
		}
	}

	.our_achievements .our_achievements_title::before {
		left: 0;
		top: 75px;
		height: 3px;
		width: 20px;
	}

	.our_achievements_carousel {
		width: 290px;
		min-height: 460px;
		margin: unset;
		margin: auto;

		.slick-dots {
			bottom: -10px;
			left: -0.8%;
		}
	}

	.our_achievements_carousel div {
		min-height: 147px;
		width: 250px;
		margin: auto;
		position: relative;
	}

	.our_achievements_carousel div span {
		display: inline-block;
		margin-top: 21px;
		margin-left: 0px;
		min-height: 66px;
		width: 250px;
		color: #333333;
		font-family: $desk-bold-proxima !important;
		font-size: 14px;
		font-weight: normal;
		line-height: 22px;
		white-space: normal;
	}

	.our_achievements_carousel div span:nth-of-type(1) {
		width: 250px;
		margin-top: 21px;
		min-height: 66px;
	}

	.our_achievements_carousel div::before {
		top: -1px;
		height: 3px;
		width: 250px;
	}
}

